/* eslint-disable prettier/prettier */
import React, {useState} from 'react';

import bg from '../../assets/bg.png';
import {View, Text, Image, Button, StyleSheet} from 'react-native';

import {FontAwesomeIcon} from '@fortawesome/react-native-fontawesome';
import {
  faBriefcase as IconeCompany,
  faMapMarkerAlt as IconeLocal,
  faBuilding as IconeSizeCompany,
  faChartBar as IconeExperienceLevel,
  faFileAlt as IconeTypeContract,
  faMoneyBillAlt as IconeSalary,
} from '@fortawesome/free-solid-svg-icons';

export interface IJob {
  id: string;
  title: string;
  avatar: string;
  name_company: string;
  user_id: string;
  remote: string;
  techs: Array<String>;
  responsibilities: string;
  requirements: string;
  benefits: string;
  types_contract: string;
  size_company: string;
  experience_level: string;
  expired_days: number;
  created_at: Date;
  salary: string;
  expired: boolean;
  apply: boolean;
}

interface Props {
  title: string;
}
export function JobContent({title}: Props) {
  const [jobs, setJobs] = useState<IJob[]>();
  console.log(bg);
  const ImageComponent = () => {
    return (
      <View>
        <Image
          source={bg}
          style={{
            width: '100%',
            height: 100,
            flex: 1,
          }}
        />
      </View>
    );
  };
  return (
    <View
      style={{
        borderWidth: 1,
        borderColor: '#d3d3d3',
        flexDirection: 'column',
        width: '100%',
        backgroundColor: '#f3f3f3',
        marginVertical: 10,
        padding: 10,
        maxWidth: '90%',
        alignSelf: 'center',
        zIndex: 0,
      }}>
      <View
        style={{
          width: '100%',
          borderBottomWidth: 1,
          borderColor: '#d3d3d3',
          paddingBottom: 10,
        }}>
        <Image
          source={bg}
          style={{
            width: '30%',
            height: 100,
            alignSelf: 'center',
          }}
        />
      </View>

      <View
        style={{
          width: '70%',
          flexDirection: 'column',
          padding: 5,
        }}>
        <View>
          <Text style={styles.title}>titulo {title}</Text>
        </View>

        <View
          style={{
            flexDirection: 'row',
            flexWrap: 'wrap',
          }}>
          <View style={styles.div}>
            <Text style={styles.span}>Grupo Globo</Text>
            <FontAwesomeIcon icon={IconeCompany} />
          </View>
          <View style={styles.div}>
            <Text style={styles.span}>Local</Text>
            <FontAwesomeIcon icon={IconeLocal} />
          </View>
          <View style={styles.div}>
            <Text style={styles.span}>Tamanho da empresa</Text>
            <FontAwesomeIcon icon={IconeSizeCompany} />
          </View>
          <View style={styles.div}>
            <Text style={styles.span}>nivel de experiencia</Text>
            <FontAwesomeIcon icon={IconeExperienceLevel} />
          </View>
          <View style={styles.div}>
            <Text style={styles.span}>CLT</Text>
            <FontAwesomeIcon icon={IconeTypeContract} />
          </View>
          <View style={styles.div}>
            <Text style={styles.span}>3000</Text>
            <FontAwesomeIcon icon={IconeSalary} />
          </View>
        </View>
      </View>
    </View>
  );
}

export const styles = StyleSheet.create({
  div: {
    flexDirection: 'row',
    alignItems: 'center',
    // borderWidth: 3,
    // borderColor: 'red',
    padding: 5,
  },
  span: {
    marginHorizontal: 5,
  },
  title: {
    color: '#000',
    fontSize: 30,
    marginHorizontal: 5,
  },
});
